$standWidth: calc(100vw / 6.55);

.row__poster {
    min-width: $standWidth;
    height: calc(#{$standWidth} / 1.8);
    margin-right: 5px;
    background-size: cover;
    border-radius: 3px;
    position: relative;
    cursor: pointer;
    &:hover {
        .row__poster_inner {
            height: 400px;
            width: 400px;
            transition-delay: .5s;
            visibility: visible;
            opacity: 1;
            top: -120px;
            left: calc((200px - #{$standWidth / 2}) * -1); } } }

.row__poster_inner {
    z-index: 7;
    width: calc(#{$standWidth} );
    height: calc(#{$standWidth} / 1.8);
    border-radius: 8px;
    overflow: hidden;
    -webkit-box-shadow: 0px 5px 7px 3px rgba(0,0,0,0.7);
    background: #141414;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
    transition: .15s ease-out;
    cursor: pointer;
    visibility: hidden; }

.btn__container {
    display: flex;
    justify-content: space-between;
    padding: 20px 15px; }

.btn__container_inner {
    display: flex;
    div {
        margin-right: 10px; } }

.poster__inner_unmute {
    position: absolute;
    right: 15px;
    top: 155px; }

.description__container {
    display: flex;
    margin-left: 10px;
    margin-bottom: 20px;
    font-size: 15px;
    font-weight: 550;
    span, div {
        margin-left: 7px; } }

.description__match {
    color: lightgreen; }

.description__age {
    border: 1px solid lightgrey;
    padding: 0 7px; }

.description__dot {
    width: 4px;
    height: 4px;
    display: inline-block;
    background: lightgrey;
    border-radius: 50%;
    align-self: center; }
