.trailer {
    height: 37vw;
    display: flex;
    background-size: cover;
    background-image: linear-gradient(rgba(17,17,17, 1), rgba(17,17,17, .7), rgba(17,17,17, .7), rgba(17,17,17, 1)), url('https://www.webbspy.com/wp-content/uploads/2020/04/money-heist-1.jpg'); }

.trailer__video {
    width: 54vw;
    height: 30.375vw;
    margin: 90px 4vw 40px 3vw;
    border-radius: 8px;
    overflow: hidden;
    position: relative; }

.trailer__video_button {
    z-index: 3;
    position: absolute;
    right: 15px;
    bottom: 50px; }

.trailer__description {
    margin-top: 90px;
    margin-right: 4vw;
    width: 33vw;
    img {
        width: 27vw;
        object-fit: contain;
        margin-bottom: 35px; }
    p {
        display: inline-block;
        font-size: 1.4vw; } }


.trailer__video_cover {
    z-index: 2;
    top: 0;
    width: 54vw;
    height: 30.375vw;
    position: absolute; }

